import React, { useState } from 'react';
import Navbar from '../components/Navbar.jsx';
import Titulo from '../components/Titulo.jsx';
import './Contato.css';

const Contato = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = (event) => {
    event.preventDefault();

    const command = `Comando: Nome=${name}, Email=${email}, Mensagem=${message}`;
    
    console.log(command);

  };

  return (
    <div>
      <Navbar />
      <Titulo title="Contato" />
      <div className="contato-container">
        <div className="contact-form">
          <h2>Entre em contato</h2>
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="name">Nome:</label>
              <input
                type="text"
                id="name"
                className="form-control"
                placeholder="Seu nome"
                value={name}
                onChange={(e) => setName(e.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="email">E-mail:</label>
              <input
                type="email"
                id="email"
                className="form-control"
                placeholder="Seu e-mail"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="message">Mensagem:</label>
              <textarea
                id="message"
                className="form-control"
                placeholder="Sua mensagem"
                rows="5"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                required
              ></textarea>
            </div>
            <button type="submit" className="btn btn-primary">Enviar</button>
          </form>
        </div>
        <div className="contact-icons">
          <a href="mailto:joaopedrotcorrea1212@gmail.com" target="_blank" rel="noopener noreferrer">
            <img className="logos" src={require('../images/gmail.png')} alt="Gmail" />
          </a>
          <a target="_blank" rel="noopener noreferrer" href="https://br.linkedin.com/in/joao-pedro-tietbohl-347823254">
            <img className="logos" src={require('../images/linkedin.png')} alt="LinkedIn" />
          </a>
          <a target="_blank" rel="noopener noreferrer" href="https://github.com/Joao-Tietbohl">
            <img className="logos" src={require('../images/github.png')} alt="GitHub" />
          </a>
        </div>
      </div>
    </div>
  );
};

export default Contato;



